.superInput {
    min-width: 150px;
    height: 40px;
    margin: 5px;
    border-radius: 5px;
    background: #bebfc4;
    border: none;
    padding: 4px;
    box-sizing: border-box;
    outline: none;
    display: block;
}

.errorInput {
    background: #d95353;
}

.error {
    color: #d95353;
}